import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { FormFieldData } from '../../interfaces/dynamic-form-option.interface';
import { AbstractFormFeild } from '../../components/abstracts/form-field.abstract';

@Component({
  selector: 'app-formfield',
  templateUrl: './formfield.component.html',
  styleUrl: './formfield.component.scss'
})
export class FormfieldComponent extends AbstractFormFeild implements OnInit {
  @Input('initData') initData!: FormFieldData;
  @Output() event = new EventEmitter<FormControl>();
  
  ngOnInit(): void {
    this.init(this.initData);
  }
}
